import { withAuthButton } from "components/hoc/withAuthButton";
import { IACLProps } from "components/interface/common";
import { Button, ButtonProps } from "primereact/button";
import { useTranslation } from "react-i18next";
import LoaderIcon from "assets/img/buttonIcons/loader.png";

// translation of label is pending

interface ITextButtonProps extends ButtonProps, IACLProps {
  args?: any;
  isLoading?: any;
}
const TextButton = (props: ITextButtonProps) => {
  const { disabled, isDisabledByAcl, isIgnoreAccessLevel, label, args, isLoading ,onClick  } = props;
  const { t } = useTranslation();

  const translatedLabel = t(label!, args);
  let disable = disabled;
  if (!isIgnoreAccessLevel) {
    if (isDisabledByAcl) disable = true;
  }

  return (
    <Button
      {...props}
      className={`text-sm text-button  ${
        disable ? "text-secondary-steelBlue-400" : "text-primary-copyTextColor"
      } ${props.className}`}
      disabled={disable }
      loadingIcon={ <img className="w-3 h-3 animate-spin mr-1.5" src={LoaderIcon} alt="loader" />}
      label={translatedLabel}
      loading={isLoading}
      iconPos={"right"}
    />
  );
};

export default withAuthButton(TextButton);
